.header__link {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-decoration: none;
  color: rgb(255, 255, 255);
}

@media screen and (max-width: 320px) {
  .header__link {
    font-size: 14px;
    line-height: 17px;
    margin-right: 30px;
  }
}