.popup-tooltip__text {
  font-family: 'Inter', sans-serif;
  font-size: 24px;
  line-height: 29px;
  font-weight: 900;
  margin: 0 0 60px 0;
  width: 350px;
  text-align: center;
}

@media screen and (max-width: 320px) {
  .popup-tooltip__text {
    font-size: 20px;
    line-height: 24px;
  }
}